<template>
  <div class="icons">
    <swiper class="swiper-container" :options="swiperOption" v-if="showSwiper">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img-container">
            <img class="icon-img" :src="item.imgUrl" alt="">
          </div>
          <p class="keywords">{{item.keywords}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  computed: {
    showSwiper () {
      return this.pages.length
    },
    pages () {
      const pages = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container
    height 0
    padding-bottom 55%
    .swiper-pagination-bullets
      bottom 0
      .swiper-pagination-bullet
        width 6px
        height 6px
  .icons
    background-color #fff
    padding-bottom .2rem
    .icon
      position relative
      overflow hidden
      float left
      width 25%
      height 0
      padding-bottom 25%
      .icon-img-container
        position absolute
        top 0
        left 0
        right 0
        bottom .3rem
        box-sizing border-box
        padding .1rem
        .icon-img
          display block
          height 100%
          margin 0 auto
      .keywords
        position absolute
        left 0
        right 0
        bottom 0
        height .3rem
        line-height .3rem
        color $darkColor
        text-align center
        ellipsis()
</style>



